.cat-item-container{
  display: flex;
  width: auto;
  align-items: center;
}

.cat-item-container img{
  height: 50px;
}

.cat-item-messages{
  display: flex;
  flex-direction: column;
  width: auto;
  align-items: flex-start;
}

.cat-item-container label{
  margin-bottom: 10px;
  font-size: 14px;
  color: #fff;
  background: #432b51;
  padding: 10px;
  border-radius: 5px;
  width: fit-content;
  font-weight: bold;
  box-sizing: border-box;
}